import { Injectable } from '@angular/core';
import { Label } from '../models/label';

@Injectable({
  providedIn: 'root'
})
export class LabelsService {

  private labels: Label[] = [];

  constructor() {
    const labelsString = localStorage.getItem('labels');

    if (labelsString) {
      this.labels = JSON.parse(labelsString);
    }
  }

  clearLabels(): void {
    this.labels = [];

    this.saveLabels();
  }

  addLabel(label: Label): void {
    this.labels = [...this.labels, label];

    this.saveLabels();
  }

  deleteLabel(label: number|Label): void {
    let labelIndex: number;

    if (typeof(label) === 'number') {
      labelIndex = label;
    } else {
      labelIndex = this.labels.indexOf(label);
    }

    this.labels.splice(labelIndex, 1);
    this.labels = [...this.labels];

    this.saveLabels();
  }

  getLabelByIndex(index: number): Label {
    return {...this.labels[index]};
  }

  updateLabelByIndex(index: number, label: Label) {
    this.labels.splice(index, 1, label);
    this.saveLabels();

    return this.labels = [...this.labels];
  }

  get labelsToPrint(): Label[] {
    return this.labels.filter((label) => label.quantity > 0);
  }

  get allLabels(): Label[] {
    return this.labels;
  }

  private saveLabels() {
    localStorage.setItem('labels', JSON.stringify(this.labels));
  }

}
