import { AfterViewInit, Component, DoCheck, ElementRef, Input, OnChanges, OnDestroy, OnInit, SimpleChanges } from '@angular/core';
import { Label } from 'src/app/models/label';
import * as moment from 'moment';
import { interval, Observable, Subscription } from 'rxjs';

@Component({
  selector: 'app-label',
  templateUrl: './label.component.html',
  styleUrls: ['./label.component.scss']
})
export class LabelComponent implements OnDestroy, OnInit {

  @Input()
  label: Label;

  fontSize: string = '12px'
  refreshInterval: Observable<number> = interval(500);
  refreshIntervalSubscription: Subscription;

  constructor(private elementRef: ElementRef<HTMLElement>) { }

  ngOnInit(): void {
    this.refreshIntervalSubscription = this.refreshInterval.subscribe(this.refreshFont.bind(this));
  }
  ngOnDestroy(): void {
    this.refreshIntervalSubscription.unsubscribe();
  }

  refreshFont() {
    const currentHeight = this.elementRef?.nativeElement?.offsetHeight || 534;
    this.fontSize = `${currentHeight/534 * 14}px`;
  }

  formatDate(date: Date): string {
    return moment(date).format('DD.MM.yyyy');
  }

}
